import * as React from "react";
import styled from "styled-components";
import { getOpenOptions, callAllowance, sendExpire, sendExercise, getLatestPrice, blockTimestamp } from "../helpers/web3";
import { BINARY_OPTIONS_CONTRACT } from "../constants/contracts";

import Up from "../assets/up.png";
import Down from "../assets/down.png";

import Button from 'src/components/Button';
import Loading from 'src/components/Loading';
import { colors } from 'src/styles';
import { convertAmountFromRawNumber } from 'src/helpers/bignumber';

const SStake = styled.div`
    width:100%;
    height:100%;
`
const SHelper = styled.div`
    font-size: x-small;
`
const STable = styled.table`
    border-collapse: collapse;
    width: 100%;
`
const STh = styled.th`
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
`

const STrEven = styled.tr`
    background-color: #dddddd;
`



interface IExerciseProps {
    address: string
    chainId: number
    web3: any
    balance: number
    decimals: number
    openExchange: any
}


interface IExerciseState {
    address: string;
    web3: any;
    chainId: number;
    pendingRequest: boolean;
    balance: number;
    decimals: number;
    error: string;
    allowance: number;
    options: [];
    currentPrice: number;
    priceInterval: any;
    optionsInterval: any;
}

const INITIAL_STATE: IExerciseState = {
    address: "",
    web3: null,
    chainId: 1,
    balance: 0,
    decimals: 0,
    pendingRequest: false,
    error: "",
    allowance: 0,
    options: [],
    currentPrice: 0,
    priceInterval: null,
    optionsInterval: null
};
class Exercise extends React.Component<any, any> {
    // @ts-ignore
    public state: IExerciseState;

    constructor(props: IExerciseProps) {
        super(props);
        this.state = {
            ...INITIAL_STATE
        };
        this.state.web3 = props.web3;
        this.state.balance = props.balance;
        this.state.address = props.address;
        this.state.decimals = props.decimals;
        this.state.chainId = props.chainId;
    }

    public componentDidMount() {
        this.loadOpenOptions();
        this.loadCurrentPrice();
        this.checkAllowance();

        this.setState({priceInterval :setInterval(() => {
            this.loadCurrentPrice()
        }, 10000)});
        this.setState({optionInterval :setInterval(() => {
            this.loadOpenOptions()
        }, 30000)});
    }


    public componentWillUnmount() {
        clearInterval(this.state.priceInterval);
        clearInterval(this.state.optionsInterval);
    }

    public async loadCurrentPrice() {
        const {chainId, web3} = this.state;
        const latestPrice = await getLatestPrice(chainId, web3);
        
          // tslint:disable-next-line:no-console
          console.log(`latestRound is ${latestPrice}`);
        this.setState({currentPrice:latestPrice});
    }

    public async loadOpenOptions() {
        const { chainId, web3 } = this.state;
        // const totalSupply = await callBalanceOf(POOL_CONTRACT[chainId].address, chainId, web3);
      
        
        const options: any = await getOpenOptions( chainId, web3);
       

        // tslint:disable-next-line:no-console
        console.log("options");
        // tslint:disable-next-line:no-console
        console.log(options);
        const optionsObjects = {};
        for(let i=0;i<options.length;i++){
            const timestamp: any = await blockTimestamp(options[i].blockNumber, web3);
            // tslint:disable-next-line:no-console
            console.log(`loaded event ${options[i]}`);
            // tslint:disable-next-line:no-console
            console.log(options[i]);
            if (options[i].returnValues.id !== undefined) {// ensurese we skip other events
                if (options[i].returnValues.strikePrice === undefined) {
                   // modifier
                    optionsObjects[options[i].returnValues.id].complete = true;
                } else {
                    optionsObjects[options[i].returnValues.id] = {
                        blockNumber: options[i].blockNumber,
                        timestamp,
                        id: options[i].returnValues.id,
                        creator: options[i].returnValues.account,
                        strikePrice: options[i].returnValues.strikePrice,
                        lockedValue: options[i].returnValues.lockedValue,
                        type: options[i].returnValues.direction,
                        complete: false
                    }
                }
            }
           
        }
        
        const sorted = Object.keys(optionsObjects).sort((a:any, b: any) => b - a);

        // tslint:disable-next-line:no-console
        console.log(`sorted $`);
        // tslint:disable-next-line:no-console
        console.log(sorted);
        const sortedOptions: any = [];
        sorted.forEach((id: any) => {
            sortedOptions.push(optionsObjects[id]);
        });

        // tslint:disable-next-line:no-console
        console.log(sortedOptions);
        this.setState({ options: sortedOptions })
    }

    public async checkAllowance() {
        const { chainId, web3, address } = this.state;

        const allowance = await callAllowance(address, BINARY_OPTIONS_CONTRACT[chainId].address, chainId, web3);
        this.setState({ allowance });
    }


    public async handleExpire(optionId: any) {
        this.setState({ pendingRequest: true });
        const { address, chainId, web3 } = this.state
        try {
            await sendExpire(address, optionId, chainId, web3, (p1: any, p2: any) => {
                        
                // tslint:disable-next-line:no-console
                console.log(p1, p2);
                this.loadOpenOptions();
                this.setState({ error: "", pendingRequest: false });
               
            });
        } catch (e) {
            this.setState({ error: "Approval Failed", pendingRequest: false });
        }
    }

    public async handleExercise(optionId: any) {
        this.setState({ pendingRequest: true });
        const { address, chainId, web3 } = this.state
        try {

                // tslint:disable-next-line:no-console
                console.log(`sending exercise for opton ${optionId}`);
            await sendExercise(address, optionId, chainId, web3, (p1: any, p2: any) => {
                        
                // tslint:disable-next-line:no-console
                console.log(p1, p2);
                this.loadOpenOptions();
                this.setState({ error: "", pendingRequest: false });
               
            });
        } catch (e) {
            this.setState({ error: "Approval Failed", pendingRequest: false });
        }
    }

    public renderOptionDirection(optionType: any) {

        // tslint:disable-next-line:no-console
        console.log(`option type ${optionType}, typ ${typeof(optionType)}`);

        if (optionType === "2") {
            return ( 
            <div style={{display: 'flex', flexDirection: 'row'}}>
    
                Call
                    <div style={{
                        height: '15px',
                        width: '15px',
                        background: `url(${Up}) no-repeat`,
                        backgroundSize: `cover`,
                        backgroundPosition: 'center',
                        margin: '4px'
                        }}/>
                </div>
            )
        } else {
            return (
                <div style={{display: 'flex', flexDirection: 'row'}}>
                Put
                    <div style={{
                        height: '15px',
                        width: '15px',
                        background: `url(${Down}) no-repeat`,
                        backgroundSize: `cover`,
                        backgroundPosition: 'center',
                        margin: '4px'
                        }}/>
                </div>
            )
        }
    }

    public renderExpireExercise(option: any) {
        const {currentPrice} = this.state;

        if (option.complete) {

            return <Button disabled>Completed</Button>;
        } else if (option.timestamp+(3600*1000) < new Date().getTime()) {
            // option ready to expire
            return <Button onClick={() => this.handleExpire(option.id)}>Expire</Button>;
        }else if (option.type === "1" && option.strikePrice > currentPrice) {
             // put option ready to exercise

                // tslint:disable-next-line:no-console
                console.log(`${option.strikePrice} stik ${currentPrice} current`);
            return <Button onClick={() => this.handleExercise(option.id)}>Exercise</Button>;
        } else if (option.type === "2" && option.strikePrice < currentPrice) {
            // call option ready to exercise
                // tslint:disable-next-line:no-console
                console.log(`${option.strikePrice} stik ${currentPrice} current`);
            return <Button onClick={() => this.handleExercise(option.id)}>Exercise</Button>;
        }  else {
            // optionwith no action available
            return <Button disabled>None</Button>;
        }
    }


    public renderOptions() {
        const {options, decimals} = this.state;
        if (options.length > 0) {
            return (
                <STable>
                    <thead> 
                        <tr>
                            <STh>ID</STh>
                            <STh>Direction</STh>
                            <STh>Fee</STh>
                            <STh>Action</STh>
                        </tr>
                    </thead>
                    <tbody>
                    {options.map((option: any, index: number)=>{
                        if (index % 2 === 0) {
                            return <STrEven key={ index }>
                                <STh>{option.id}</STh>
                                <STh>{this.renderOptionDirection(option.type)}</STh>
                                <STh>{convertAmountFromRawNumber(option.lockedValue*0.1, decimals)} BIOP</STh>
                                <STh>{this.renderExpireExercise(option)}</STh>
                            </STrEven>;
                        } else {
                            return <tr key={ index }>
                                <STh>{option.id}</STh>
                                <STh>{this.renderOptionDirection(option.type)}</STh>
                                <STh>{convertAmountFromRawNumber(option.lockedValue*0.1, decimals)} BIOP</STh>
                                <STh>{this.renderExpireExercise(option)}</STh>
                            </tr>;
                        }
                       
                      })}
                      </tbody>
                </STable>
            )
        } else {
            return (
                <p>A moment please, the current options are loading...:)</p>
            )
        }
       
    }






    public render() {
        const { pendingRequest, error,  } = this.state;
        return (
            <SStake>

                <h1>Exercise</h1>
                <p>Earn a fee for exercising options ITM or unlocking expired options.</p>
                <SHelper style={{ color: `rgb(${colors.red})` }}>{error}</SHelper>
                {
                    pendingRequest ?
                        <Loading />
                        :
                        this.renderOptions()
                }
              

            </SStake>

        )
    }

}

export default Exercise