import * as React from "react";
import styled from "styled-components";
import { callPoolTotalSupply, callPoolStakedBalance, callAllowance, sendApprove, callPoolLastStake, callPoolMaxAvailable } from "../helpers/web3";
import { BINARY_OPTIONS_CONTRACT } from "../constants/contracts";

import { makeBet } from "../helpers/web3";

import Input from 'src/components/Input';
import Button from 'src/components/Button';
import BetButton from 'src/components/BetButton';
import Loading from 'src/components/Loading';
import PriceChart from 'src/components/PriceChart';
import { colors } from 'src/styles';
import { convertAmountFromRawNumber, greaterThan, convertAmountToRawNumber } from 'src/helpers/bignumber';

const SBet = styled.div`
    width:100%;
    height:100%;
`
const SHelper = styled.div`
    font-size: x-small;
`

const SBetter = styled.div`
    display: flex;
    flex-direction: row;
    height: 1000px;
    width: 450px;
`

const SBetButtonContainer = styled.div`
    display: flex;
    flex-direction: column;
`;

interface IBetProps {
    address: string
    chainId: number
    web3: any
    balance: number
    decimals: number
    openExchange: any
    reloadBalance: any
}


interface IBetState {
    address: string;
    web3: any;
    chainId: number;
    pendingRequest: boolean;
    balance: number;
    decimals: number;
    error: string;
    betAmount: number;
    allowance: number;
    maxBet: number;

}

const INITIAL_STATE: IBetState = {
    address: "",
    web3: null,
    chainId: 1,
    balance: 0,
    decimals: 0,
    pendingRequest: false,
    error: "",
    allowance: 0,
    betAmount: 0,
    maxBet: 0
};
class Bet extends React.Component<any, any> {
    // @ts-ignore
    public state: IBetState;

    constructor(props: IBetProps) {
        super(props);
        this.state = {
            ...INITIAL_STATE
        };
        this.state.web3 = props.web3;
        this.state.balance = props.balance;
        this.state.address = props.address;
        this.state.decimals = props.decimals;
        this.state.chainId = props.chainId;
    }

    public componentDidMount() {
        this.getStaked();
        this.checkAllowance();
    }

    public async getStaked() {
        const { chainId, web3, address } = this.state;
        // const totalSupply = await callBalanceOf(POOL_CONTRACT[chainId].address, chainId, web3);

        const staked = await callPoolStakedBalance(address, chainId, web3);
        const lastStake = await callPoolLastStake(address, chainId, web3);
        const totalStaked = await callPoolTotalSupply(chainId, web3);
        const maxBet = await callPoolMaxAvailable(chainId, web3);

        // tslint:disable-next-line:no-console
        console.log("presend");
        // tslint:disable-next-line:no-console
        console.log(lastStake);
        this.setState({ totalStaked, staked, lastStake, maxBet });
    }

    public async checkAllowance() {
        const { chainId, web3, address } = this.state;

        const allowance = await callAllowance(address, BINARY_OPTIONS_CONTRACT[chainId].address, chainId, web3);
        this.setState({ allowance });
    }


    public async handleApprove() {
        this.setState({ pendingRequest: true });
        const { address, chainId, web3 } = this.state
        try {
            await sendApprove(address, BINARY_OPTIONS_CONTRACT[chainId].address, chainId, web3, (param1: any, param2: any) => {
                this.setState({ error: "", pendingRequest: false });
                this.checkAllowance();
            });
        } catch (e) {
            this.setState({ error: "Approval Failed", pendingRequest: false });
        }
    }

    public async handleMakeBet(direction: boolean) {
        const {betAmount, web3, chainId, address, decimals} = this.state;
        
        this.setState({ pendingRequest: true });
        try {
            await makeBet(address,  convertAmountToRawNumber(betAmount, decimals), direction, chainId, web3, (param1: any, param2: any) => {
                this.props.reloadBalance();
                this.setState({ error: "", pendingRequest: false });
            });
            
        } catch (e) {
            this.setState({ error: "Approval Failed", pendingRequest: false });
        }
    }


    public renderMaxBet() {
        const {maxBet} = this.state;
        if (maxBet === 0) {
            return <SHelper>Pool Maxed Out</SHelper>
        } else {
            return <SHelper>Max Bet Size: {maxBet}</SHelper>
        }
    }



    public renderBetApprove() {
        const { allowance, betAmount, web3, decimals } = this.state;
        if (betAmount > 0) {
            if (greaterThan(convertAmountToRawNumber(betAmount, decimals), allowance)) {
                return (
                    <div>
                        <Button onClick={() => this.handleApprove()}>(1) Approve</Button>

                    </div>
                )
            } else {
                return (
                    <SBetter>
                        <PriceChart web3={web3}/>
                        <SBetButtonContainer>
                            <BetButton up={true} onClick={()=>{this.handleMakeBet(true)}}/>
                            <BetButton up={false} onClick={()=>{this.handleMakeBet(false)}}/>
                        </SBetButtonContainer>
                    </SBetter>
               )// TODO show bet buttons here
            }
        } else {
            return (<></>)
        }
    }




    public render() {
        const { balance, decimals, pendingRequest, error, betAmount } = this.state;
        const {openExchange} = this.props;
        return (
            <SBet>

                <h1>Bet</h1>
                <p>Play the game anon. Just play the game.</p>
                <SHelper style={{ color: `rgb(${colors.red})` }}>{error}</SHelper>
                <SHelper>Enter a comfy bet size to begin</SHelper>
                {
                    pendingRequest ?
                        <Loading />
                        :
                        <>
                            <Input value={betAmount} placeholder={`Amount To Bet`} onChange={(e: any) => this.setState({ betAmount: e.target.value })} id="amountStake" />
                            {this.renderMaxBet()}
                           
                            <br />
                            {this.renderBetApprove()}
                            <SHelper style={{ color: `rgb(${colors.red})` }}>
                                You Have <b>{`${convertAmountFromRawNumber(balance, decimals)}`}</b> BIOP. <b style={{cursor: "pointer"}} onClick={() => openExchange()}>Get More.</b>
                            </SHelper>
                        </>
                }


            </SBet>

        )
    }

}

export default Bet