import * as React from "react";
import styled from "styled-components";

import { callCalculatePurchaseReturn, callCalculateSaleReturn, sendBuy, sendSell } from "../helpers/web3";
import Button from 'src/components/Button';
import Input from 'src/components/Input';
import Switch from 'src/components/Switch';
import Loading from 'src/components/Loading';
import {
    convertAmountFromRawNumber,
    isNumber,
    convertToDecimals
} from '../helpers/bignumber'
import { colors } from "../styles";

const SExchange = styled.div`
    width:100%;
    height:100%;
`

const SHelper = styled.div`
    font-size: x-small;
`


interface IExchangeProps {
    address: string
    chainId: number
    web3: any
    balance: number
    decimals: number
}


interface IExchangeState {
    buying: boolean;
    address: string;
    web3: any;
    chainId: number;
    amountToRecieve: any;
    amountToSend: any;
    pendingRequest: boolean;
    balance: number;
    decimals: number;
    error: string;
}

const INITIAL_STATE: IExchangeState = {
    amountToSend: 0,
    address: "",
    web3: null,
    chainId: 1,
    balance: 0,
    decimals: 0,
    amountToRecieve: 0,
    buying: true,
    pendingRequest: false,
    error: ""
};
class Exchange extends React.Component<any, any> {
    // @ts-ignore
    public state: IExchangeState;

    constructor(props: IExchangeProps) {
        super(props);
        this.state = {
            ...INITIAL_STATE
        };
        this.state.web3 = props.web3;
        this.state.balance = props.balance;
        this.state.address = props.address;
        this.state.decimals = props.decimals;
        this.state.chainId = props.chainId;
    }

    public renderAmountToRecieve() {
        const { amountToRecieve, buying, decimals } = this.state;
        if (buying) {
            return (
                <div>You Will Recieve ~{
                    convertAmountFromRawNumber(amountToRecieve, decimals)}
                     BIOP</div>
            )
        }
        return (
            <div>You Will Recieve ~{amountToRecieve} ETH</div>
        )
    }

    public async updateAmountToRecieve(amountSend: any) {
        const { buying, chainId, web3 } = this.state;
        // tslint:disable-next-line
        console.log(parseFloat(amountSend) != NaN);
        // tslint:disable-next-line
        if (parseFloat(amountSend) && parseFloat(amountSend) > 0 && isNumber(amountSend)) {
            // amountSend = new BigNumber(amountSend);
            // tslint:disable-next-line
            console.log(`created bn ${amountSend} ${typeof (amountSend)}`);
            try {
                if (buying) {
                    const amount = await callCalculatePurchaseReturn(web3.utils.toWei(`${amountSend}`, "ether"), chainId, web3);
                    this.setState({ amountToRecieve: amount, error: "" });
                } else {
                    const amount = await callCalculateSaleReturn(amountSend, chainId, web3);
                    const inETH = web3.utils.fromWei(amount, "ether");
                    this.setState({ amountToRecieve: inETH, error: "" });
                }
            } catch (e) {

                this.setState({ error: "Amount to recieve update failed" });
            }

        }
    }

    public async onInputChange(e: any) {
        this.setState({ amountToSend: e.target.value });
        await this.updateAmountToRecieve(e.target.value);
    }

    public async exchange() {
        //  const {buying, amountToSend, web3, chainId, address} = this.state;
        const { buying, amountToSend, web3, chainId, address, amountToRecieve, decimals } = this.state;
        if (amountToSend > 0) {
            this.setState({ pendingRequest: true });

            if (buying) {

                await sendBuy(address, web3.utils.toWei(amountToSend, "ether"), amountToRecieve, chainId, web3, (param1: any, param2: any) => {

                    this.setState({ pendingRequest: false });
                // tslint:disable-next-line
                location.reload();
                });
            } else {
                await sendSell(address, convertToDecimals(amountToSend, decimals), chainId, web3, (param1: any, param2: any) => {

                    this.setState({ pendingRequest: false });
                    // tslint:disable-next-line
                    location.reload();
                    });
            }

        }

    }

    public renderBuySellSwitch() {
        const { buying } = this.state;
        return (
            <Switch
                vertical={false}
                on={buying}
                onTap={() => { this.setState({ buying: true, amountToSend: "" }) }}
                onLabel="Buy"
                offTap={() => { this.setState({ buying: false, amountToSend: "" }) }}
                offLabel="Sell"
            />

        )
    }



    public render() {
        const { buying, balance, decimals, pendingRequest, amountToSend, error } = this.state;
        return (
            <SExchange>
                <h1>Exchange</h1>
                {pendingRequest ?
                    <>
                        <Loading />
                        <p>Sending Tx...</p>
                    </>
                    :
                    <>

                        {this.renderBuySellSwitch()}
                        <SHelper>{buying ? "ETH" : "BIOP"} to send</SHelper>
                        <Input value={amountToSend} placeholder="Amount To Send" onChange={(e: any) => this.onInputChange(e)} id="amountToSend" />
                        <SHelper style={{ color: `rgb(${colors.red})` }}>
                            You Have <b>{`${convertAmountFromRawNumber(balance, decimals)}`}</b> BIOP
                        </SHelper>
                        {this.renderAmountToRecieve()}
                        <Button onClick={() => this.exchange()}>{buying ? "BUY" : "SELL"}</Button>
                        <p>{error}</p>
                    </>
                }
            </SExchange>
        )
    }
}

/* Exchange.propTypes = {
    web3: PropTypes.any.isRequired,
    chainId: PropTypes.number.isRequired,
    address: PropTypes.string.isRequired,
    balance: PropTypes.number,
    decimals: PropTypes.number
} */

export default Exchange
