import { BONDING_CURVE_CONTRACT, POOL_CONTRACT, BINARY_OPTIONS_CONTRACT, PRICE_PROVIDER_CONTRACT } from '../constants'
import {bigNumberStringToInt} from "./bignumber";


const zeroAddress = "0x0000000000000000000000000000000000000000";

// general functions
export function blockTimestamp(blockNumber: string, web3: any) {
  return new Promise(async(resolve, reject) => {
    try {
      const block = await web3.eth.getBlock(blockNumber);
      
          // tslint:disable-next-line:no-console
          console.log(` raw block is ${block}`);
      resolve(block.timestamp*1000);
    } catch(e) {
      reject(e);
    }
  })
}

export function getLatestPrice( chainId: number, web3: any) {
  return new Promise(async(resolve, reject) => {
    const biop = getPriceProviderContract(chainId, web3)
    await biop.methods
      .latestRoundData()
      .call(
        { from: zeroAddress },
        (err: any, data: any) => {
          if (err) {
            reject(err)
          }

          resolve(data.answer);
        }
      )
  })
}

export function getPriceProviderContract(chainId: number, web3: any) {
  const biop = new web3.eth.Contract(
    PRICE_PROVIDER_CONTRACT[chainId].abi,
    PRICE_PROVIDER_CONTRACT[chainId].address
  )
  return biop;
}

export function getBondingCurveTokenContract(chainId: number, web3: any) {
  const biop = new web3.eth.Contract(
    BONDING_CURVE_CONTRACT[chainId].abi,
    BONDING_CURVE_CONTRACT[chainId].address
  )
  return biop;
}

export function getPoolERC20Contract(chainId: number, web3: any) {
  const pool = new web3.eth.Contract(
    POOL_CONTRACT[chainId].abi,
    POOL_CONTRACT[chainId].address
  );
  return pool;
}

export function getBinaryOptionsContract(chainId: number, web3: any) {
  const bin = new web3.eth.Contract(
    BINARY_OPTIONS_CONTRACT[chainId].abi,
    BINARY_OPTIONS_CONTRACT[chainId].address
  );
  return bin;
}

// BIOP token functions
export function callBalanceOf(address: string, chainId: number, web3: any) {
  return new Promise(async(resolve, reject) => {
    const biop = getBondingCurveTokenContract(chainId, web3)
    await biop.methods
      .balanceOf(address)
      .call(
        { from: zeroAddress },
        (err: any, data: any) => {
          if (err) {
            reject(err)
          }

          // tslint:disable-next-line:no-console
          console.log(`biop raw balance is ${data}`);
          resolve(data)
        }
      )
  })
}

