export function callCalculatePurchaseReturn(amount: number, chainId: number, web3: any) {
  return new Promise(async(resolve, reject) => {
    const biop = getBondingCurveTokenContract(chainId, web3)
    const soldAmount:any = await callSoldAmount(chainId, web3);
    // tslint:disable-next-line:no-console
    console.log(`calculate purchase return with amount ${amount}, ts ${soldAmount}`);
    await biop.methods
      .s(soldAmount, amount+soldAmount)
      .call(
        { from: zeroAddress },
        (err: any, data: any) => {
          if (err) {
            reject(err)
          }

          resolve(data)
        }
      )
  })
}

export function callCalculateSaleReturn(amount: any, chainId: number, web3: any) {
  return new Promise(async(resolve, reject) => {
    const biop = getBondingCurveTokenContract(chainId, web3)
    const soldAmount = await callSoldAmount(chainId, web3);
    // tslint:disable-next-line:no-console
    console.log(`calculate purchase return with amount ${amount}, ts ${soldAmount}`);
    
    await biop.methods
      .s(web3.utils.toBN(amount), soldAmount)
      .call(
        { from: zeroAddress },
        (err: any, data: any) => {
          if (err) {
            reject(err)
          }

          resolve(data)
        }
      )
  })
}


