export function sendBuy(address: string, weiToSpend: number, amountToRecieve: number, chainId: number, web3: any, onComplete: any) {
  return new Promise(async(resolve, reject) => {
    
    const biop = getBondingCurveTokenContract(chainId, web3)
    await biop.methods.buy(amountToRecieve).send({
      from: address,
      value: weiToSpend,
    }, (err: any, data: any) => {
      if (err) {
        reject(err)
      }

      resolve(data)
    })

    .on('confirmation', onComplete)
    .on('error', onComplete)
  })
}

