// binary options functions
export function makeBet(address: string, amount: string, callOption: boolean, chainId: number, web3: any, onComplete: any) {
  return new Promise(async(resolve, reject) => {
    const bin = getBinaryOptionsContract(chainId, web3);
    const type = callOption ? 2 : 1;// this order may be wrong
    const formattedAmount = bigNumberStringToInt(amount);
    // tslint:disable-next-line:no-console 
   console.log(`bet amuynt raw type ${typeof(amount)}`);
     // tslint:disable-next-line:no-console 
    console.log(`bet amuynt raw ${amount}`);
     // tslint:disable-next-line:no-console
    console.log(`bet amuynt raw ${formattedAmount}`);

    const poolLockedAmount = await getPoolLockedAmount(chainId, web3);
    // tslint:disable-next-line:no-console
    console.log(`pool locked amount${poolLockedAmount}`);
    const poolBalance = await callBalanceOf(POOL_CONTRACT[chainId].address, chainId, web3);
    // tslint:disable-next-line:no-console
    console.log(`pool balacne${poolBalance}`);
   // const possiblePaout = await getPossiblePayout(amount, web3, chainId);
        // tslint:disable-next-line:no-console
        console.log(`formatted amount ${formattedAmount}`);
        // tslint:disable-next-line:no-console
     //   console.log(`possible payout ${possiblePaout}`);
    await bin.methods
      .create(formattedAmount, type)
      .send(
        { from: address },
        (err: any, data: any) => {
          if (err) {
            reject(err)
          }

          resolve(data)
        }
      )
      .on('confirmation', onComplete)
      .on('error', onComplete)
  })
}

