export function getOpenOptions(chainId: number, web3: any) {
  return new Promise(async(resolve, reject) => {
    const bin = getBinaryOptionsContract(chainId, web3)
    await bin.getPastEvents('allEvents', {// 'create' evemt
    fromBlock: 'genesis', 
    toBlock: 'latest'}, (error: any, events: any) => {
      if (error) {
        reject(error);
      }
      resolve(events);
    })
})
}
