import * as React from "react";
import styled from "styled-components";
import { callPoolTotalSupply, callPoolStakedBalance, callAllowance, sendApprove, callPoolLastStake, sendDeposit} from "../helpers/web3";
import {POOL_CONTRACT} from "../constants/contracts";

import Input from 'src/components/Input';
import Switch from 'src/components/Switch';
import Button from 'src/components/Button';
import Loading from 'src/components/Loading';
import { colors } from 'src/styles';
import { convertAmountToRawNumber, convertAmountFromRawNumber, greaterThan } from 'src/helpers/bignumber';

const SStake = styled.div`
    width:100%;
    height:100%;
`
const SHelper = styled.div`
    font-size: x-small;
`

interface IStakeProps {
    address: string
    chainId: number
    web3: any
    balance: number
    decimals: number
    reloadBalance: any
}


interface IExchangeState {
    address: string;
    web3: any;
    chainId: number;
    pendingRequest: boolean;
    balance: number;
    decimals: number;
    error: string;
    staked: number;
    totalStaked: number;
    allowance: number;
    changeAmount: any;// amount to stake or unstake
    staking: boolean;
    lastStake: number;
}

const INITIAL_STATE: IExchangeState = {
    address: "",
    web3: null,
    chainId: 1,
    balance: 0,
    decimals: 0,
    pendingRequest: false,
    error: "",
    staked: 0,
    totalStaked: 0,
    allowance: 0,
    changeAmount: 0,
    staking: true,
    lastStake: 0
};
class Stake extends React.Component<any, any> {
    // @ts-ignore
    public state: IExchangeState;

    constructor(props: IStakeProps) {
        super(props);
        this.state = {
            ...INITIAL_STATE
        };
        this.state.web3 = props.web3;
        this.state.balance = props.balance;
        this.state.address = props.address;
        this.state.decimals = props.decimals;
        this.state.chainId = props.chainId;
    }

    public componentDidMount() {
        this.getStaked();
        this.checkAllowance();
    
    }

    public async getStaked() {
        const {chainId, web3, address} = this.state;
        // const totalSupply = await callBalanceOf(POOL_CONTRACT[chainId].address, chainId, web3);
        
        const staked = await callPoolStakedBalance(address, chainId, web3);
        const lastStake = await callPoolLastStake(address, chainId, web3);
        const totalStaked = await callPoolTotalSupply(chainId, web3);


            // tslint:disable-next-line:no-console
            console.log("presend");
            // tslint:disable-next-line:no-console
            console.log(lastStake);
        this.setState({totalStaked, staked, lastStake})
    }

    public async checkAllowance() {
        const {chainId, web3, address} = this.state;

        const allowance = await callAllowance(address, POOL_CONTRACT[chainId].address, chainId, web3);
        
            // tslint:disable-next-line:no-console
            console.log(`got allowance ${allowance}`);
        this.setState({allowance});
    }

    public async handleWithdraw() {
        this.setState({pendingRequest: true});
        const {changeAmount} = this.state;
        if (changeAmount > 0) {

        // TODO handle withddraw
        this.setState({pendingRequest: false, error: ""});

        location.reload(true);
        } else {
            this.setState({pendingRequest: false, error: "Can't withdraw 0"});
        }
    
    }

    public async handleApprove() {
        this.setState({pendingRequest: true});
        const {address, chainId, web3} = this.state
        try {
            await sendApprove(address, POOL_CONTRACT[chainId].address, chainId, web3, async (param1: any, param2:any)=>{
                // tslint:disable-next-line:no-console
                console.log(param1);
                // tslint:disable-next-line:no-console
                console.log(param2);
                
                await this.getStaked();
                await this.checkAllowance();
                this.setState({error: "", pendingRequest: false});
                
            });
        } catch (e) {
            this.setState({error: "Approval Failed", pendingRequest: false});
        }
    }

    public async handleDeposit() {
        this.setState({pendingRequest: true});
        const {address, chainId, web3, changeAmount, balance, decimals} = this.state;

        if (balance < changeAmount) {
            this.setState({error: "insufficent BIOP balance", pendingRequest: false});
        } else {
            await sendDeposit(address, convertAmountToRawNumber(changeAmount, decimals), chainId, web3, (param1: any, param2: any) => {
                // tslint:disable-next-line:no-console
                console.log("confirmed");
                // tslint:disable-next-line:no-console
                console.log(param1);
                // tslint:disable-next-line:no-console
                console.log(param2);
                this.getStaked();
                this.props.reloadBalance();
                this.setState({pendingRequest: false, error: "", changeAmount: 0});
                
    
            });
            
        }
    }

    public renderWithdrawAvailable() {
        const {lastStake} = this.state;
        if (lastStake !== 0) {
            const unlock = new Date(lastStake*1000+1209600000);// add 14 days
            const now = new Date();

            if (unlock.getTime() > now.getTime()) {
                return (
                    <div>
                        <SHelper style={{color: `rgb(${colors.red})`}}>
                            Withdraw available: {unlock.toLocaleString()}</SHelper>
                        <br/>
                        <Button disabled>Withdraw</Button>
                    </div>
                    )
            } else {
                return (
                    <Button onClick={() => this.handleWithdraw()}>Withdraw</Button>
                )
            }     
        } else {
            return (<></>);
        }
        
    }

    public renderStakeWithdrawSwitch() {
        const { staking } = this.state;
        return (
            <Switch 
                vertical={false} 
                on={staking} 
                onTap={() => { this.setState({ staking: true, changeAmount: "", error: "" })}}
                onLabel="Stake"
                offTap={() => { this.setState({ staking: false, changeAmount: "", error: "" }) }}
                offLabel="Withdraw"
                />
            
        )
    }

    public renderApproveStake() {
        const {allowance, changeAmount, decimals} = this.state;
        if (changeAmount > 0) {

                // tslint:disable-next-line:no-console
                console.log(`allowance ${allowance} changeAmount ${convertAmountToRawNumber(changeAmount, decimals)}`);
            if (greaterThan(convertAmountToRawNumber(changeAmount, decimals), allowance)) {
                return (
                    <div>
                        <Button onClick={() => this.handleApprove()}>(1) Approve</Button>
                         ➡️
                        <Button disabled={true}>(2) Stake</Button>
                    </div>
                )
            } else {
                return (
                    <div>
                        <Button disabled={true}>(✅) Approve</Button>
                        ➡️
                        <Button onClick={() => this.handleDeposit()}>(2) Stake</Button>
                    </div>
                )
            }
        } else {
            return (<></>)
        }
    }


    public render() {
        const {totalStaked, staked, balance, decimals, changeAmount, staking, pendingRequest, error} = this.state;
        return(
            <SStake>

                <h1>Stake</h1>
                <p>Use BIOP to earn passive premiums on binary option bets.</p>
                <p>Your stake: <b>{convertAmountFromRawNumber(staked, decimals)}</b></p>
                <p><b>{((staked/totalStaked))*100}%</b> of total staked.</p> 
                <br/>
                {this.renderStakeWithdrawSwitch()}
                <SHelper style={{color: `rgb(${colors.red})`}}>{error}</SHelper>
                <SHelper style={{ color: `rgb(${colors.red})` }}>
                    You Have <b>{`${convertAmountFromRawNumber(balance, decimals)}`}</b> BIOP 
                </SHelper>
                {
                    pendingRequest ?
                    <Loading/>
                    :
                    <>
                    <Input value={changeAmount} placeholder={`Amount To ${staking ? "Stake" : "Withdraw"}`} onChange={(e: any) => this.setState({ changeAmount: e.target.value })} id="amountStake" />
                    <br/>
                    {staking ?
                    this.renderApproveStake()
                    :
                    this.renderWithdrawAvailable()
                    }
                    </>
                }

               
            </SStake>

        )
    }

}

export default Stake